.Helper {
  color: #282c34;
  text-align: left;
  font-family:    Arial, Helvetica, sans-serif;
  background-color: white;
  padding: 2%;
}

.App-link {
  color: #61dafb;
}

.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
  border-style: solid;
  border-width: 5px;
  border-color: white;
}

.wrapper canvas {
  position: absolute;
}



 .container {
  position: relative;
  width: 100%;
  /* margin-left: 2%; */
}

.container img {
  width: 100%;
  height: auto;
}

.container .btn {
  position: absolute;
  top: 90%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  background-color: rgb(165, 165, 165);
  border-style: solid;
  border-width: 2px;
  border-color: rgb(255, 255, 255);
  font-size: 16px;
  cursor: pointer;
  height: 12%;
  width: 9%;
  border-radius: 50%;
  outline: 0;
  opacity: 0.9;
}

.container .btn:hover {
  background-color: #cd2626;
  outline: 0;
} 


.container .helpButton {
  position: absolute;
  top: 88%;
  right: 0%;
  opacity: 0.5;
  height: 10%;
  width: 10%;
}

.container .helpButton:hover {
  outline: 0;
  opacity: 1;
} 


.container .closeButton {
  position: absolute;
  bottom: 80%;
  left: 83.0%;
  opacity: 0.5;
  height: 15%;
  width: 15%;
  padding: 2%;
}

.container .closeButton:hover {
  outline: 0;
  opacity: 1;
} 


.container .saveButton {
  position: absolute;
  top: 88%;
  left: 0%;
  opacity: 0.5;
  height: 10%;
  width: 10%;
}

.container .saveButton:hover {
  outline: 0;
  opacity: 1;
} 


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
} 


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

